import React, { useState } from "react";
import { useLocation, Route, Switch, Redirect } from "react-router-dom";
// reactstrap components
import { Container } from "reactstrap";
// core components
import SearchNavbar from "components/SearchNavbar.js";
// import DetailNavbar from "components/DetailNavbar.js";
import Footer from "components/Footer.js";
import Sidebar from "components/Sidebar.js";
import { HASH_TAG_SUB, HASH_TAG } from '../Portpolio';
import List from "views/List.js";
import Detail from "views/Detail.js";
import DetailFull from "views/DetailFull.js";

const Layout = () => {
  const mainContent = React.useRef(null);
  const location = useLocation();
  const [chkCondition, setChkCondition] = useState(Object.keys(HASH_TAG_SUB));
  const [radioCondition, setRadioCondition] = useState('');
  const [searchText, setSearchText] = useState('');
  const [searchCount, setSearchCount] = useState(0);

  React.useEffect(() => {
    document.documentElement.scrollTop = 0;
    document.scrollingElement.scrollTop = 0;
    mainContent.current.scrollTop = 0;
  }, [location]);

  return (
    <>
      <Sidebar chkCondition={chkCondition} setChkCondition={setChkCondition} radioCondition={radioCondition} setRadioCondition={setRadioCondition} />
      <div className="main-content" ref={mainContent}>
        {location?.pathname === '/list' ? <SearchNavbar setSearchText={setSearchText} searchCount={searchCount} /> : null}
        <Switch>
          <Route path="/list" component={() => <List chkCondition={chkCondition} radioCondition={radioCondition} searchText={searchText} setSearchCount={setSearchCount} />} />
          <Route path="/detail/:name" component={(props) => <Detail {...props} />} />
          <Route path="/detail_full/:name" component={(props) => <DetailFull {...props} />} />
          <Redirect from="*" to="/list" />
        </Switch>
        <Container fluid>
          <Footer />
        </Container>
      </div>
    </>
  );
};

export default Layout;
