import React, { useRef } from "react";
import Portpolio from '../Portpolio';
import PortpolioImage from '../PortpolioImage';
import { Swiper, SwiperSlide } from 'swiper/react';
import { Link } from "react-router-dom";

import '../assets/css/view/detail.css';
import 'swiper/swiper.scss';
import 'swiper/swiper-bundle.css';

const Detail = (props) => {
  const swiper = useRef(null);
  const name = props?.match?.params?.name;
  const detail = Portpolio[name];
  let images = PortpolioImage.getImageAll(name);
  
  const goNext = () => {
    if (swiper.current !== null && swiper.current.swiper !== null) {
      swiper.current.swiper.slideNext();
    }
  };
  const goPrev = () => {
    if (swiper.current !== null && swiper.current.swiper !== null) {
      swiper.current.swiper.slidePrev();
    }
  };

  return (
    <div className="container" >
      <div className="desc-container">
        <div className="title">
          {detail.name}
          <button className="btnBack" onClick={props.history.goBack}>뒤로</button>
        </div>
        <div className="desc-row date">날짜: {detail.start_date}</div>
        <div className="desc-row date">기간: {detail.working_peoriod}</div>
        <div className="desc-row desc">인원: {detail.member_count}명</div>
        <div className="desc-row desc">
          <span>상세내용</span>
          {Object.keys(detail.contents).map((key, index) => {
            const contents = detail.contents[key];

            return (
              <div key={key}>
                <div className="content-title">[{key}]</div>
                <div className="content-desc">{contents}</div>
              </div>
            );
          })}
        </div>
      </div>

      <div className="image-container">
        <Swiper
          ref={swiper}
          spaceBetween={50}
          slidesPerView={1}
          navigation={{
            nextEl: '.swiper-button-next',
            prevEl: '.swiper-button-prev',
          }}
          pagination={{
            el: '.swiper-pagination',
            type: 'bullets',
            clickable: true
          }}
          loop={true}
          // centeredSlides={true}
        >
          {images && Object.keys(images).map((key, index) => {
            let image = images[key];

            return (
              <SwiperSlide key={key} >
                <Link to={`/detail_full/${name}`}>
                  <img className="img-portpolio" src={image.default} alt={key} />
                </Link>
              </SwiperSlide>
              );
          })}
          <div className="swiper-pagination" />
          <div className="swiper-button-prev" onClick={goPrev} />
          <div className="swiper-button-next" onClick={goNext} />
        </Swiper>
      </div>
    </div>
  );
};

export default Detail;
