import React, { useEffect, useRef } from "react";
import PortpolioImage from '../PortpolioImage';
import { Swiper, SwiperSlide } from 'swiper/react';
import '../assets/css/view/detail_full.css';
import 'swiper/swiper.scss';
import 'swiper/swiper-bundle.css';

const DetailFull = (props) => {
  const swiper = useRef(null);
  const name = props?.match?.params?.name;
  let images = PortpolioImage.getImageAll(name);
  
  const goNext = () => {
    if (swiper.current !== null && swiper.current.swiper !== null) {
      swiper.current.swiper.slideNext();
    }
  };
  const goPrev = () => {
    if (swiper.current !== null && swiper.current.swiper !== null) {
      swiper.current.swiper.slidePrev();
    }
  };

  // ESC 누르면 뒤로가기
  const handleKeyPress = (e) => {
    if (e.key === 'Escape') {
      props.history.goBack();
    }
  }

  useEffect(() => {
    document.addEventListener('keydown', handleKeyPress);

    return () => {
      document.removeEventListener('keydown', handleKeyPress);
    };
  }, []);

  return (
    <div className="full-swiper" >
        <button className="full-swiper-close" onClick={props.history.goBack}>X</button>

        <Swiper
            ref={swiper}
            spaceBetween={50}
            slidesPerView={1}
            navigation={{
            nextEl: '.swiper-button-next',
            prevEl: '.swiper-button-prev',
            }}
            pagination={{
            el: '.swiper-pagination',
            type: 'bullets',
            clickable: true
            }}
            loop={true}
        >
            {images && Object.keys(images).map((key, index) => {
            let image = images[key];

            return (<SwiperSlide key={key} ><img className="img-portpolio" src={image.default} alt={key} /></SwiperSlide>);
            })}
            <div className="swiper-pagination" />
            <div className="swiper-button-prev" onClick={goPrev} />
            <div className="swiper-button-next" onClick={goNext} />
        </Swiper>
    </div>
  );
};

export default DetailFull;
