import React from "react";
import { Link } from "react-router-dom";
// reactstrap components
import {
  Form,
  FormGroup,
  InputGroupAddon,
  InputGroupText,
  Input,
  InputGroup,
  Navbar,
  Container,
} from "reactstrap";
import { debounce } from 'lodash';

const SearchNavbar = ({setSearchText, searchCount}) => {
  // 디바운스
  const debounceChangeText = debounce((text) => {
    setSearchText(text);
  }, 500);

  return (
    <>
      <Navbar className="navbar-top navbar-dark" expand="md" id="navbar-main">
        <Container fluid>
          <span className="h4 mb-0 text-white text-uppercase d-none d-lg-inline-block">Portpolio List (총 {searchCount}개)</span>
          <Form className="navbar-search navbar-search-dark form-inline mr-3 d-none d-md-flex ml-lg-auto">
            <FormGroup className="mb-0">
              <InputGroup className="input-group-alternative">
                <InputGroupAddon addonType="prepend">
                  <InputGroupText>
                    <i className="fas fa-search" />
                  </InputGroupText>
                </InputGroupAddon>
                <Input placeholder="Search" type="text" onChange={e => debounceChangeText(e.target.value)}/>
              </InputGroup>
            </FormGroup>
          </Form>
        </Container>
      </Navbar>
    </>
  );
};

export default SearchNavbar;
