import Portpolio from './Portpolio';

 const UTIL = {
    // start_date를 기준으로 정렬된 포트폴리오 배열 키 리스트
    sortedPortPolioKey: () => {
        return Object.keys(Portpolio).sort((a,b) => {
            return new Date(Portpolio[b]['start_date']).getTime() - new Date(Portpolio[a]['start_date']).getTime();
        });
    }
}

export default UTIL;