import React, { useState, useEffect, useRef } from "react";
import { Link } from "react-router-dom";
import {
  Collapse,
  Input,
  NavbarBrand,
  Navbar,
  Container,
  Row,
  Col,
  Label
} from "reactstrap";
import { HASH_TAG, HASH_TAG_MAIN, HASH_TAG_SUB } from '../Portpolio';
import '../assets/css/components/sidebar.css';

const Sidebar = ({chkCondition, setChkCondition, radioCondition, setRadioCondition }) => {
  const [collapseOpen, setCollapseOpen] = useState();
  const allChk = Object.keys(HASH_TAG_SUB).length === chkCondition?.length;
  const searchBar = useRef(null);

  // toggles collapse between opened and closed (true/false)
  const toggleCollapse = () => {
    setCollapseOpen((data) => !data);
  };
  // closes the collapse
  const closeCollapse = (e) => {
    // 서치바가 보여지고 있으면서 서치바 다른곳을 클릭하면 닫음
    if(document.querySelector('.navbar-collapse').classList.contains('show') && !e.target.closest('.navbar-collapse')) {
      setCollapseOpen(false);
    }
  };

  const changeChk = (key, checked) => {
    if(checked) {
      const index = chkCondition.indexOf(key);
      chkCondition.splice(index, 1);
      setChkCondition([...chkCondition]);
    } else {
      setChkCondition([...chkCondition, key]);
    }
  }

  const chkAll = () => {
    if(allChk) {
      setChkCondition([]);
    } else {
      setChkCondition(Object.keys(HASH_TAG_SUB));
    }
  }

  useEffect(() => {
    document.addEventListener('click', closeCollapse);

    return () => {
      document.removeEventListener('click', closeCollapse);
    };
  }, []);

  return (
    <Navbar
      className="navbar-vertical fixed-left navbar-light bg-white"
      expand="md"
      id="sidenav-main"
    >
      <Container fluid>
        {/* Toggler */}
        <button
          className="navbar-toggler"
          type="button"
          onClick={toggleCollapse}
        >
          <span className="navbar-toggler-icon" />
        </button>

        <NavbarBrand className="pt-0 logo-text" to="/list" tag={Link}>
          <img alt="logo" className="navbar-brand-img logo-image" src={require("../assets/img/brand/profile.png").default} />
          Nalshya
        </NavbarBrand>
        
        <Collapse navbar isOpen={collapseOpen}>
          <div className="navbar-collapse-header d-md-none" ref={searchBar}>
            <Row>
              <Col className="collapse-brand" xs="6">
                <Link to={'/list'} className="logo-text" >
                  <img className="logo-image" alt="logo" src={require("../assets/img/brand/profile.png").default} />
                  Nalshya
                </Link>
              </Col>
              <Col className="collapse-close" xs="6">
                <button
                  className="navbar-toggler"
                  type="button"
                  onClick={toggleCollapse}
                >
                  <span />
                  <span />
                </button>
              </Col>
            </Row>
          </div>

          <div className="div-checkbox main">
            <Label className="radio-hashtag bold">
              <Input type="radio" onChange={() => setRadioCondition('')} checked={radioCondition === ''} /> 전체
            </Label>

            {Object.keys(HASH_TAG_MAIN).map((tag, index) => {
              var check = radioCondition === tag;
              
              return (
                <Label key={'checked_' + tag} className="radio-hashtag" >
                  <Input type="radio" onChange={() => setRadioCondition(tag)} checked={check} /> {HASH_TAG_MAIN[tag]}
                </Label>
            )})}
          </div>

          

          <div className="div-checkbox sub">
            <Label className="check-hashtag bold">
              <Input type="checkbox" onChange={() => chkAll(allChk)} checked={allChk} /> 전체
            </Label>
            
            {Object.keys(HASH_TAG_SUB).map((tag, index) => {
              var check = chkCondition?.includes(tag);
              
              return (
              <Label key={'checked_' + tag} className="check-hashtag" >
                <Input type="checkbox" onChange={() => changeChk(tag, check)} checked={check} /> {HASH_TAG_SUB[tag]}
              </Label>
            )})}
          </div>
        </Collapse>
      </Container>
    </Navbar>
  );
};

export default Sidebar;
