import React, { useEffect } from "react";
import { Link } from "react-router-dom";
import Portpolio, { HASH_TAG } from '../Portpolio';
import Util from '../Util';
import '../assets/css/view/index.css';

const List = ({chkCondition, radioCondition, searchText, setSearchCount}) => {
  let listCount = 0;

  useEffect(() => {
    setSearchCount(listCount);
  }, [chkCondition, searchText]);

  return (
    <div className="header bg-gradient-info pb-8 pt-5 pt-md-8">
        <div className="div-row" >
        {Util.sortedPortPolioKey().map((key, index) => {
          const row = Portpolio[key];
          const isMatchMainTag = radioCondition == row.maintag || radioCondition == '';
          const isMatchSubTag = row.hashtag.some((tag) => chkCondition.includes(String(tag)));
          const isMatchSearchText = row.name.includes(searchText) || row.summary.includes(searchText);

          const isShow = !row.hide && isMatchMainTag && isMatchSubTag && isMatchSearchText;
          listCount = isShow ? listCount + 1 : listCount;
          return isShow ? (
            <div key={`${'col_' + index}`} lg="6" xl="3">
              <div className="card-stats mb-4 mb-xl-0 card">
                  <div className="div-card-contents" >
                    <div className="col">
                      <span className="h2 font-weight-bold mb-0">
                      {row['name']}
                      </span>
                    </div>
                    <div className="col div-card-summary">
                      {row['summary']}
                    </div>
                    
                  <Link to={`/detail/${key}`}>
                    <div className="col-auto">
                      <img className="img-main" src={require(`../assets/portpolio/${key}/01.png`).default} alt={key} />
                    </div>
                  </Link>
                  </div>
                <div className="div-hash-tag">
                  {[row.maintag, ...row.hashtag].map((tag, index) => {
                    let mainTagStyle = tag.toString().substr(0, 1) === '1' ? 'main-tag' : '';
                    return <span key={'tag_' + index} className={'span-hash-tag ' + mainTagStyle} >#{HASH_TAG[tag]}</span>;
                  })}
                </div>
              </div>
            </div>
          ) : null;
        })}
        </div>
    </div>
  );
};

export default List;
